$colours: "oneToOne"#C2E2BF,
"white"#ffff,
"buyer"#BDDFF6,
"general"#D1D3D4,
"listing"#FCF7BC,
"openHome"#F37D80,
"marketReview"#FFDFBA,
"rental"#F8B1B9,
"rentalOpenHome"#D0BEDD,
"sellerVisit"#AB9ECC,
"tenant"#D789BA,
"virtualListing"#88CFBD,
"virtualBuyer"#8ABDE6,
"brand"#822433,
"darkRed"#4C0E1B,
"darkGrey"#414042,
"darkBlue"#C0D9E6,
"lightBlue"#EFF9FE,
"green"#2BB673,
"lightGrey"#A7A9AC,
"landlord"#F8B1B9,
"prospect"#FCF7BC,
"seller"#FFDFBA,
"servicer"#D1D3D4;



@each $i in $colours {
    .#{nth($i, 1)}-background {
        background: nth($i, 2) !important;
    }

    .#{nth($i, 1)}-color {
        color: nth($i, 2) !important;
    }

    .#{nth($i, 1)}-border-left {
        border-left: 4px solid nth($i, 2);
        height: -webkit-fill-available;
    }

    .#{nth($i, 1)}-button {
        background-color: nth($i, 2) !important;


        &:hover {
            background-color: darken(nth($i, 2), 10%) !important;
        }

        &.active {
            background-color: darken(nth($i, 2), 10%) !important;
            color: black !important;
            font-weight: bold;
        }
    }

    .#{nth($i, 1)}-icon-button {
        color: nth($i, 2) !important;

        &:hover {
            color: darken(nth($i, 2), 10%) !important;
        }

        &.active {
            color: darken(nth($i, 2), 10%) !important;
        }
    }

   

    .themeButton-toggle-group {

        &.#{nth($i, 1)} {
            width: 100%;

            label {

                &.active {
                    background-color: nth($i, 2) !important;
                    color: black !important;
                    font-weight: bold;
                }

                &:hover {
                    background-color: nth($i, 2) !important;
                    color: black !important;
                    font-weight: bold;
                }

                &:focus {
                    box-shadow: 0 0 0 0.2rem nth($i, 2) !important;
                }
            }

        }
    }
}


.white-outline-button {
    background-color: #fff !important;
    color: #D1D3D4 !important;
    border: 2px solid #D1D3D4;
    font-weight: bold;

    &:hover {
        background-color: #D1D3D4 !important;
        color: #fff !important;
    }
}

.dot {
    margin-right: 1rem;
    height: 1rem;
    width: 1rem;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;

    &.green {
        background-color: #2BB673;
    }

    &.red {
        background-color: #F37D80;
    }

    @each $i in $colours {
        &.#{nth($i, 1)}s {
            background-color: nth($i, 2);
        }
    }
}


table {
    @each $i in $colours {
        &.#{nth($i, 1)} {

            border: 2px solid nth($i, 2) !important;

            thead {
                background-color: nth($i, 2) !important;
            }

        }
    }
}

input[type='radio']:checked:after {
    background-color: kt-state-color(brand) !important;
}

.default-icon-button {
    color: kt-state-color(wb-light-grey) !important;

    &:hover {
        color: kt-state-color(wb-light-grey) !important;
    }

    &.active {
        border: 0px;
        color: kt-state-color(brand) !important;
    }

    &.focus {
        border: 0px;
        box-shadow: unset;
    }
}

.tableTheme {


    overflow-y: auto;
    max-height: 75vh;

    @each $i in $colours {
        &.#{nth($i, 1)} {
            border: 3px solid nth($i, 2);

            thead {
                background-color: nth($i, 2) !important;
            }

            table td+td {
                border-left: 2px solid nth($i, 2) !important;
            }

            thead {
                border: 2px solid nth($i, 2) !important;

                th {
                    background: nth($i, 2) !important;
                }
            }

        }
    }

    &.selected {
        td {
            color: kt-state-color(wb-light-grey);
        }
    }

    table td+td {
        border-left: 2px solid #82263382;


    }

    thead {
        border: 2px solid #822633;

        th {
            background: #822633;
            z-index: 3;
            text-align: center;
            position: sticky;
            top: 0;
            color: #fff;
        }
    }

    tbody {
        tr {
            td:nth-child(1) {
                padding-left: 1.5rem;
            }

        }
    }
}

.table-responsive {
    overflow: scroll;

    thead tr:nth-child(1) th {
        background: white;
        position: sticky;
        top: 0;
        z-index: 10;
    }
}

.border-right {
    border-right: 1px solid kt-state-color(wb-light-grey);
}

