// tableFixHead


.tableFixHead {

    border: 3px solid #822633;
    overflow-y: auto;
    height: 70vh;


    table td+td {
        border-left: 2px solid #82263382;


    }

    thead {
        tr:nth-child(1) {
            display: none;
        }

        th {
            background: #822633;
            z-index: 1;
            text-align: center;
            position: sticky;
            top: 0;
            color: #fff;
        }
    }

    tbody {
        tr {
            td:nth-child(1) {
                padding-left: 1.5rem;
            }

            i {
                padding: 5px;
            }

        }
    }

    tfoot {
        tr:last-child {
            display: none;
        }

        td {
            background: kt-state-color(wb-dark-blue);
            position: -webkit-sticky;
            position: sticky;
            bottom: 0;

            z-index: 4;
        }
    }
}



.tbody-scrollable {
    display: block;
    overflow: auto;

    tr {
        display: block;

        td {
            display: block;
        }
    }
}

.row-selected {
