// Note Form
.note-form {
    position: relative;

    .dictaphone {
        right: 0px;
        top: 10px;
        position: absolute;

    }

    .add-tags {
        position: absolute;
        top: 34px;
        right: 0px;
        padding: 6px;

        & i {
            font-size: 15px;
        }
    }

    .add-note {
        position: absolute;
        top: 65px;
        right: 0px;
        padding: 6px;
    }
}


.hoverEffect {
    position: relative;

    .hoverEffectMain {
        opacity: 1;
        display: block;
        transition: .5s ease;
        backface-visibility: hidden;
    }

    .hoverEffectItem {
        transition: .5s ease;
        opacity: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        text-align: center;
    }

    &:hover {
        .hoverEffectMain {
            opacity: 0.3;
        }

        .hoverEffectItem {
            opacity: 1;
        }
    }
}

