.contact-profileHeader {
  text-align: end;
}



.contact-personalInfo {
  text-align: center;
  margin-top: 20px;
}

.contact-personalInfo-name {
  color: #5a7391;
  font-size: 16px;
  font-weight: 600;
  // margin-bottom: 7px;
}

.contact-personalInfo-job {
  text-transform: uppercase;
  color: #7c83aa;
  font-size: 12px;
  font-weight: 600;
  margin-bottom: 15px;
}

.contact-socialButtons {
  text-align: center;
}

.contact-socialButtons button {
  text-transform: uppercase;
  font-size: 11px;
  font-weight: 600;
  padding: 6px 5px;
  margin-right: 5px;
}

.contact-socialButtons button:last-child {
  margin-right: 0px;
}

/* Profile Content */
.contact-content {
  padding: 20px;
  background: #fff;
  min-height: 460px;
}

.contact-profileBasicInfo {
  margin-bottom: 10px;

  .row {
    margin-bottom: 3px;
    margin-top: 3px;
  }

  p{
    margin-bottom: 0px;
  }
}

.contact-connections {
  margin-bottom: 10px;

  .MuiList-root {
    padding-left: 0px;
  }
}

.contact-editButton {
  text-align: end;
  margin-bottom: -33px;
}

.note-timeline {

  .MuiTimelineItem-missingOppositeContent:before {
    display: none;
  }

}

.contact-modal {

  .modal-body {
    padding-top: 0px !important;
  }

  .dictaphone {}

  .add-note {
    top: 120px;
    right: 0px;
    position: absolute;

  }

  .MuiIconButton-root:hover {
    background-color: unset;
  }

  .card>hr {
    margin-right: auto;
    margin-left: auto;
  }
}

.connections {
  .MuiAvatar-root {
    width: 20px;
    height: 20px;
    font-size: inherit;
  }

  .relation {
    // margin: auto;
  }
}

.note-list.card {
  padding-top: 10px;
  height: 75vh;

  .note-item {
    background: #ffffff4d;
    /* margin-bottom: 5px; */
    padding: 10px;
    border-left: 5px solid royalblue;

    /* border-left: 5px solid; */
    .kt-svg-icon {
      height: 15px;
      width: 15px;
    }
  }

}

.table-checkbox {
  padding: 0px !important;
}

.contactModalForm {
  .tabs {}
}


.contactsFiles-files {
  .card {
    .card-body {
      .file-type {
        i {
          font-size: 50px;
        }
      }
      button {
        padding: 5px;
        padding-top: 0px;
    margin-top: 0px;

    align-items: unset;
      }
    }
  }
}